<template>
  <div>
    <div class="container">
      <div class="mt-5">
        <div class="h1 mb-4">
          Square Play
        </div>

        <div class="levels">
          <div class="row">
            <div class="col-3">
              <div class="levels-nav">
                <div class="levels-nav-item" @click="selectLevel(level)" v-for="(level, i) in Object.keys(puzzles)" :key="i" :class="{ active: level === currentLevel }">
                  {{ level }}
                </div>
              </div>
            </div>

            <div class="col-9">
              <div class="puzzle-list d-flex flex-wrap">
                <div class="puzzle-box" v-for="(item, i) in puzzles[currentLevel]" :key="i" @click="selectPuzzle(i + 1)">
                  <div class="puzzle-box-title">
                    {{ i + 1 }}. {{ item.title }}
                  </div>
                  <div
                    class="puzzle-box-body d-flex align-items-center justify-content-center"
                  >
                    <div
                      class="puzzle-box-grid d-flex flex-wrap"
                      :class="`puzzle-box-${item.grid.x}`"
                    >
                      <div
                        v-for="(path, index) in item.data"
                        :key="index"
                        class="puzzle-path"
                        :class="
                          `option-box-${path.color[0] +
                            (path.color[1] ? '-' + path.color[1] : '')}`
                        "
                        :style="{ transform: 'rotate(' + path.rotate + 'deg)' }"
                      ></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",

  data() {
    return {
      list: [
        {
          rotate: false,
          grid: {
            x: 4,
            y: 4,
          },
          data: [
            {
              color: ["blue", "yellow"],
              rotate: 0,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue"],
              rotate: null,
            },
            {
              color: ["yellow"],
              rotate: null,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue", "yellow"],
              rotate: 270,
            },
            {
              color: ["blue", "yellow"],
              rotate: 0,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue"],
              rotate: null,
            },
            {
              color: ["yellow"],
              rotate: null,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue", "yellow"],
              rotate: 270,
            },
            {
              color: ["blue", "yellow"],
              rotate: 0,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue"],
              rotate: null,
            },
            {
              color: ["yellow"],
              rotate: null,
            },
          ],
        },
        {
          rotate: false,
          grid: {
            x: 3,
            y: 2,
          },
          data: [
            {
              color: ["blue", "yellow"],
              rotate: 0,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue"],
              rotate: null,
            },
            {
              color: ["yellow"],
              rotate: null,
            },
            {
              color: ["blue", "yellow"],
              rotate: 90,
            },
            {
              color: ["blue", "yellow"],
              rotate: 270,
            },
          ],
        },
      ],
    };
  },

  computed: {
    puzzles() {
      return this.$store.state.puzzles
    },
    currentLevel() {
      return this.$route.params.level
    }
  },

  methods: {
    selectLevel(level) {
      this.$router.replace(`/${level}`)
    },
    selectPuzzle(puzzleNumber) {
      this.$router.push(`/${this.currentLevel}/${puzzleNumber}`)
    }
  },

  mounted() {
    if (this.currentLevel == null) {
      const firstLevel = Object.keys(this.puzzles)[0];

      this.$router.replace(`/${firstLevel}`)
    }
  }
};
</script>
